.btn-bg-light, .btn-bg-dark, a.href-hover, .href-hover {
    background: none;
    border: none;
    padding: 0;
    font-family: inherit;
    color: inherit;
    cursor: pointer;
    font-size: var(--fontSize);
    font-weight: 600;
    transition: all .2s;
    &:focus {
        outline: none;
    }
}
.btn-bg {
    display: flex;
    flex-shrink: 0;
    color: #fff;
    cursor: pointer;
    padding: 11px 14px;
    border-radius: var(--borderRadius);
    background-color: var(--brandColor);
    transition: all .2s;
    &:hover {
        background-color: var(--brandColorDark);
    }
}
a.href-hover, .href-hover {
    &:hover {
        color: var(--brandColor);
    }
}